.courses-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 80px;
  width: 100%;
  max-width: 1600px;
}

.courses-grid {
  margin-top: 100px;
  margin-bottom: 100px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 50px;
  
}

.course-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: 0.3s;
  width: 250px;
  height: 120px;
  text-align: center;
  position: relative;
  cursor: pointer;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.course-card:hover {
  transform: scale(1.05);
  background: linear-gradient(135deg, #6a11cb, #2575fc);
}

.course-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  position: relative;
}

.course-index {
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  position: absolute;
  top: 10px;
}

.course-title {
  margin-top: 30px;
}

.course-name {
  margin-top: 30px;
  font-size: 18px;
}

.course-description {
  opacity: 0;
  transition: opacity 0.4s ease;
  font-size: 18px;
  top: 10%;
  transform: translateY(-55%);
}

.course-card:hover .course-name {
  opacity: 0;
}

.course-card:hover .course-description {
  opacity: 1;
}

.course-card.locked {
  opacity: 0.5;
}

.course-card.locked:hover {
  transform: none;
  background: rgba(255, 255, 255, 0.05);
}

.course-card.locked {
  opacity: 1;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.3);
  position: relative;
}

.lock-icon {
  font-size: 20px;
  color: rgba(255, 255, 255, 0.7);
  position: absolute;
  left: 8px;
  top: 8px;
}

.pagination-search-wrapper {
  position: absolute;
  top: 140px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 1200px;
  min-height: 60px;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
}

.pagination-container {
  display: flex;
  justify-content: center;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4px;
  width: 35%;
  min-width: 200px;
  gap: 5px;
}

.pagination button {
  padding: 8px 14px;
  font-size: 15px;
  border-radius: 4px;
  border: 1px solid #ccc;
  background-color: white;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.pagination button:hover {
  background-color: #ddd;
}

.pagination button:disabled {
  background-color: #bbb;
  cursor: not-allowed;
}

.pagination button.active {
  background-color: blue;
  color: white;
  font-weight: bold;
}

.search-container {
  display: flex;
}

.search-input {
  border-radius: 4px;
  border: 1px solid #ccc;
}
